.myers-datatable .table-container {
  display: flex;
  flex-direction: column;
}
.myers-datatable .table-row {
  display: flex;
  width: -moz-fit-content;
  width: fit-content;
  position: relative;
}
.myers-datatable .table-cell {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.myers-datatable .table-header-row {
  z-index: 1;
}
.myers-datatable .spinner {
  border: 4px solid #f3f3f3; /* Light grey */
  border-top: 4px solid #7e7e7e; /* Blue */
  border-radius: 50%;
  width: 15px;
  height: 15px;
  animation: spin 1s linear infinite;
  margin-left: 5px;
  position: sticky;
  left: 0px;
}
.myers-datatable .spinner-loading-text {
  margin-left: 10px;
  color: rgb(37, 37, 37);
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.myers-datatable .spinner-container {
  position: absolute;
  background-color: white;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
}
.myers-datatable .spinner-wrapper {
  display: flex;
  align-items: center;
  position: sticky;
  left: 0px;
}/*# sourceMappingURL=index.css.map */